import React, {Component} from "react";
import {Link} from "react-router-dom";

import fetchJsonp from 'fetch-jsonp'
import {Row, Col, Spin, Alert, Card} from 'antd';

export default class MovieList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      movieList: [],
      isLoaded: false
    }
  }
  componentWillMount() {
    // this.getMovieList(this.props)
  }
  componentWillReceiveProps(props) {
    this.getMovieList(props)
  }

  render() {
    if (this.state.isLoaded) {
      return <Row>
        {this
          .state
          .movieList
          .map((item, i) => {
            return (
              <Col span={6} key={i}>
                <Link to={"/movie/detail/" + item.id}>
                  <Card
                    hoverable
                    style={{
                    height: 500,
                    width: 300
                  }}
                    cover={< img alt = "example" style = {{ height: 300}}src = {
                    item.images.small
                  } />}>
                    <div>
                      <p>{item.title}</p>
                      <p>{item
                          .genres
                          .join('')}</p>
                      <p>{item.year}</p>
                      <p>{item.year}</p>

                    </div>
                  </Card>
                </Link>
              </Col>
            )
          })}
      </Row>

    } else {
      return <Spin tip="Loading..." size="large">
        <Alert type="info" style={{
          height: 80
        }}/>
      </Spin>
    }
  }
  getMovieList(props) {
    this.setState({isLoaded: false})
    const url = "https://api.douban.com/v2/movie/" + props.match.params.movieType
    fetchJsonp(url).then((response) => {
      return response.json()
    }).then((json) => {
      console.log(this, '------------', json)
      this.setState({movieList: json.subjects, isLoaded: true})

    }).catch((ex) => {
      console.log('parsing failed', ex)
    })
  }

}


