import "./App.css";

// 
import Home from "./components/home/home";
import Movie from "./components/movie/movie";
import About from "./components/about/about";

import React, {
  Component
} from 'react';


import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { Layout, Menu } from 'antd';
const { Header, Content, Footer } = Layout;
const {Item}=Menu;



class App extends Component {
  render() {
    return ( 
    <Router>
      <div className = "App">
        <Layout className="layout">
        <Header className="header" style={{background:'yellowGreen' }}>
          <div className="logo" />
          <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={['2']}
            style={{ lineHeight: '70px',background:'yellowGreen' }}
          >
            <Item key="1"><Link to="/"></Link></Item>
            <Item key="2"><Link to="/movie/in_theaters"></Link></Item>
            <Item key="3"><Link to="/about" ></Link></Item>
          </Menu>
        </Header>
        <Content className="content">     
          <Route exact path="/" component={Home}></Route>
          <Route path="/movie" component={Movie}></Route>
          <Route path="/about" component={About}></Route>
        </Content>
        <Footer style={{ textAlign: 'center' }}>
          Ant Design 2016 Created by Ant UED
        </Footer>
      </Layout>
      </div>
    </Router>
    );
  }
}

export default App;


