import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import MovieList from "./movieList.jsx";
import movieDetail from "./movieDetail.jsx"

// import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import { Layout, Menu} from 'antd';
const { Content, Sider } = Layout;




export default class Home extends Component{
  render(){
    return (
      
      <Router>
       <div className="movie">
        <Layout>
    
          <Content>
            
            <Layout>
              <Sider width={200} style={{ background: '#fff' }}>
                <Menu
                  mode="inline"
                  defaultSelectedKeys={['1']}
                  defaultOpenKeys={['sub1']}
                  style={{ height: '100%' }}>
                    <Menu.Item key="1">
                    <Link to="/movie/in_theaters"></Link>
                    </Menu.Item>
                  <Menu.Item key="2">
                    <Link to="/movie/coming_soon"></Link>
                  </Menu.Item>
                  <Menu.Item key="3">
                    <Link to="/movie/top250">top250</Link>
                  </Menu.Item>
                    
                </Menu>
              </Sider>
              <Content style={{ padding: '24px', minHeight: 280,backgroundColor:'#fff' }}>
                <Route exact path="/movie/:movieType" component={MovieList}></Route>
                <Route exact path="/movie/detail/:movieId" component={movieDetail}></Route>
              </Content>
            </Layout>
          </Content>
    
        </Layout>
       </div> 
       </Router> 
    )
  }
}


